}

/*  Correct shimmer styling */
.skeleton {
  position: relative;
  overflow: hidden;
  background: hsl(var(--muted));
  border-radius: var(--radius);
}

.skeleton::after {
  content: "";
  position: absolute;
  top: 0;
  left: -150%;
  height: 100%;
  width: 150%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
